.video-container {
  //background: #111d42;
  background-image: url("https://img.hdtoday.tv/xxrz/1200x600/394/b2/2b/b22b2ff973691c479836e96ab665c281/b22b2ff973691c479836e96ab665c281.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  width: 100%;

  padding: 50px 50px 100px;

  @media (max-width: 575.98px) {
    padding: 25px 25px 75px;
  }

  video{
    width: auto;
    max-width: 100%;
    max-height: 400px;

    @media (min-width: 992px) {
      width: 100%;
      height: auto;
      max-height: none;
    }
  }
}

.video-info{
  margin-top: -50px;
  z-index: 2;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,.05);
  padding: 25px;

  .img-container{

    @media (max-width: 767.98px) {
      img{
        max-height: 300px;
        object-fit: cover;
        object-position: top;
      }
    }

    @media (min-width: 768px) {
      max-width: 250px;
    }
    @media (min-width: 992px) {
      max-width: 270px;
    }
  }

  .elements{
    .row-line, a{
      color: #111;
      font-weight: 400;
      font-weight: 300;
    }

    strong{
      font-weight: 500;
    }
  }
}
