/* styles.css */

body {
  font-family: 'Roboto', sans-serif;
}

.bg-dark-blue {
  background-color: #111d42;
}

.banner {
  width: 100%;
  height: 300px;
  background-color: #111d42;
  display: flex;
  justify-content: center;
  align-items: center;

  h1{
    color: #ffffff;
    margin-bottom: 30px;
  }
}

.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-input {
  width: 70%;
  margin-right: 10px;
}

.search-button {
  font-family: 'Roboto', sans-serif;
}

.description-text{
  overflow: hidden;
  margin: 5px 0 0;
  font-size: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  color: #3a3a3a;
}

.movies-list-container {
  display: flex;
  flex-wrap: wrap;

  .movie-item {
    width: 50%;
    display: inline-block;
    margin-bottom: 15px;

    @media (min-width: 640px) {
      width: 33.3%;
    }
    @media (min-width: 768px) {
      width: 25%;
    }
    @media (min-width: 1200px) {
      width: calc(100% / 6);
    }
    @media (min-width: 1400px) {
      width: calc(100% / 8);
    }

    .card {
      width: 100%;
      border: 0;

      .img-container{
        position: relative;

        img{
          border-radius: 10px;
        }
        .quality{
          position: absolute;
          top: 10px;
          right: 10px;
          display: inline-block;
          background-color: #ffffff;
          padding: 2px 4px;
          font-size: 11px;
          font-weight: 600;
          border-radius: 2px;
          color: #111d42;
        }

        .hover-element{
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          transition: all ease-in 0.5s;
          display: flex;
          align-items: end;
          border-radius: 10px;


          div{
            background: #111d42d1;
            border-radius: 10px;
            display: block;
            padding: 10px;

            p{
              @extend .description-text;
              color: #c7c7c7;
              -webkit-line-clamp: 3;
              padding: 0;
              //background: linear-gradient(0deg, #575757, rgba(0, 0, 0, 0));
            }
          }
        }

        &:hover{
          .hover-element{
            opacity: 1;
          }
        }
      }

      .card-body{
        background: #fafafa;
        padding: 0;

        h3{
          color: #111d42;
          font-weight: bold;
          font-size: 14px !important;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          line-height: 1;
          margin-bottom: 7px;
        }

        .card-text{
          font-size: 11px !important;
          line-height: 1;
          color: #333;
          //font-weight: bold;

        }
        .cast{
          font-size: 11px;
          color: #373737;
          margin-top: 3px;
          line-height: 1;
          margin-top: 3px;
          //font-weight: bold;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }

        p{
          @extend .description-text;
        }

      }
    }
  }

}


.mini-footer {
  background:#192027;
  text-align:center;
  padding:32px 0
}
.mini-footer p {
  margin:0;
  line-height:26px;
  font-size:15px;
  color:#999
}
.mini-footer p a {
  color: #54659a
}
.mini-footer p a:hover {
  color:#34bfa3
}

